/*Aligns landing page logo and button*/
body { 
  background-color: #eeeee4;
}

.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/*landing page logo*/
.logo {
  width: 750px; 
  height: auto;
  margin-bottom: -100px;
}

/*Landing Page Get started button*/
.button {
  padding: 20px 30px; 
  font-family: 'Lato', sans-serif; 
  font-weight: bold; 
  font-size: 16px; 
  letter-spacing: 1px;
  border: none;
  background-color: #3c6729; 
  color: white;
  cursor: pointer;
  border-radius: 30px;
  text-decoration: none;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.box {
  width: 200px;
  height: 200px;
  background-color: #3c6729;
  margin: 10px;
}

.back-button {
  display: block;
  margin-top: 20px;
  text-decoration: none;
  color: #3c6729;
}
